.item {
    width: 80%;
    margin: 100px auto;
    padding: 30px 0;
    background-color: rgba(48, 48, 48, 0.605);
    border-radius: 4px;
    /* border: 2px solid rgb(198, 0, 0); */
    /* box-shadow: 0 0 8px rgb(198, 0, 0),  inset 0 0 8px rgb(198, 0, 0); */
}

.name {
    color: white;
    font-size: 40px;
    text-align: center;
    margin: 0 0 20px 0;
    font-family: 'Josefin Sans', sans-serif;
}

.description {
    color: white;
    text-align: justify;
    font-size: 24px;
    max-width: 60%;
    margin: auto;
    font-family: 'Josefin Sans', sans-serif;
}

.image {
    max-width: 40%;
    margin: auto;
}

.image img {
    width: 100%;
}

.form {
    display: flex;
    justify-content: center;
    font-size: 40px;
}

.form input {
    width: 100px;
    height: 50px;
    box-sizing: border-box;
    border: none;
    outline: none;
    font-size: 20px;
}

.form button {
    width: 50px;
    height: 50px;
    margin: 0 0 0 10px;
    border: none;
    outline: none;
    font-size: 20px;
    background-color: rgb(198, 0, 0);
    color: white;
}

.form button:active {
    box-shadow: 0 0 8px rgb(198, 0, 0);
}

@media (max-width: 700px) {
    .description {
        font-size: 16px;
        max-width: 80%;
        text-align: center;
    }

    .item {
        margin: 50px auto;
    }

    .form input {
        width: 60px;
        height: 30px;
    }
    
    .form button {
        width: 30px;
        height: 30px;
        margin: 0 0 0 10px;
    }
}