.cart-button {
    border: none;
    outline: none;
    border-radius: 50%;
    background-color: rgb(198, 0, 0);
    width: 60px;
    height: 60px;
    position: sticky;
    bottom: 25px;
    left: 25px;
}

.cart-button:active {
    box-shadow: 0 0 8px rgb(198, 0, 0);
}

.cart-icon {
    fill: white;
    width: 35px;
    margin: 5px 1px 0 0;
}

.cart-icon path {
    stroke-width: 0;
}

@media (max-width: 700px) {
    .cart-button {
        width: 40px;
        height: 40px;
        bottom: 15px;
        left: 15px;
    }

    .cart-icon {
        width: 20px;
    }
}